.spin::before {
  content: "";
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 5px solid #cfd0d1;
  border-bottom-color: #1c87c9;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -40px;
  margin-top: -40px;
  animation: 1.5s linear infinite spinner;
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.hidden {
  display: none;
}

.spin {
  position: fixed;
  width: 100%;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  /* background-color: rgba(255,255,255,0.7); */
  background-color: rgb(168, 168, 168,0.7);
  z-index: 9999;
}
